@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  /* font-family: "League Spartan", sans-serif; */
  font-optical-sizing: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  word-spacing: 1.1px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.active {
  font-weight: 700;
}

.txt-grad {
  background: -webkit-linear-gradient(30deg, #098bac, #1b68dd);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.separate-bottom {
  background: linear-gradient(transparent, #060d1e);
}

.separate-top {
  background: linear-gradient(#0c0d21, transparent);
}

.violet-gradient {
  background: linear-gradient(rgb(8 145 178), transparent);
}

.primary-gradient {
  background: linear-gradient(#05101f, rgb(7, 7, 29), rgb(15, 18, 36));
}

.primary-shadow {
  filter: drop-shadow(0px -1px 15px #9c9c9c42);
}
.card-gradient {
  background: linear-gradient(
    50deg,
    rgb(11, 15, 34),
    rgb(15, 21, 53),
    rgb(23, 29, 58)
  );
  /* box-shadow: 0px -1px 15px 4px rgba(40, 186, 230, 0.103); */
  border: 1px solid #ffffff57;
}

.port-input {
  width: 100%;
  padding: 7px;
  box-shadow: 0px 0px 0px black;
  border: 1px solid #72727231;
  outline: 0px solid black;
  color: #f3f3f3;
  border-radius: 5px;
  /* border: 1px solid white; */
  background: #72727231;
}

.port-input::placeholder {
  font-size: 0.8em;
}

.port-input:focus,
.port-input:hover {
  border: 1px solid #ffffff9c;
}

label {
  font-weight: 600;
}

.float:nth-child(1n) {
  animation: float 6s ease-in-out infinite;
}

.float:nth-child(2n) {
  animation: floatbottom 6s ease-in-out infinite;
}

.separator{
  background: linear-gradient(180deg, #000000, transparent);

}
.separator-bottom{
  background: linear-gradient(0deg, #000000, transparent);

}

@keyframes float {
  0% {
    transform: translatey(0px);
  }

  50% {
    transform: translatey(-7px);
  }

  100% {
    transform: translatey(0px);
  }
}

@keyframes floatbottom {
  0% {
    transform: translatey(0px);
  }

  50% {
    transform: translatey(7px);
  }

  100% {
    transform: translatey(0px);
  }
}
